import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import './Unity.css';


class Unity extends Component {
  render() {

    const { t, i18n } = this.props;

    return (
      <div className="Unity" style={{background: this.props.unity.backgroundColor}}>
        <div className="Unity-container">
          <div className="Unity-logoContainer">
            <img className="Unity-logo" src={this.props.unity.logo} alt={this.props.unity.name}></img>
            <p style={{color: this.props.unity.color}} >{t(this.props.unity.title)}</p>
          </div>
          <div className="Unity-iconContainer">
            <img style={{width: this.props.width}} className="Unity-icon" src={this.props.unity.icon} alt={this.props.unity.name}></img>
          </div>
        </div>
      </div>
    );
  }
}

export default withTranslation()(Unity);
